.home {
    flex-grow: 1;
    min-width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.home .title {
    width: 100%;
    font-size: 18px;
    text-align: center;
    font-family: "Righteous", cursive;
}

.home h1 {
    font-family: 'Source Code Pro', monospace;
    color: #fff;
    font-weight: bold;
}

.home .title p {
    line-height: 50px;
    color: #fff;
    font-weight: 600;
    font-size: 3rem;
}

#name {
    line-height: 50px;
    font-weight: 600;
    font-size: 3.5rem;
    color: #a7a29c;
}

.button {
    border: none;
    outline: none;
    cursor: pointer;
    font-size: 24px;
    margin-top: 30px;
    color: #ffffff;
    padding: 10px 15px;
    border-radius: 999px;
    background: #a7a29c;
    font-family: 'Source Code Pro', monospace;
}

.person {
    width: 100%;
    margin-top: 20px;
    border-radius: 50%;
}

.imgDev {
    width: 100%;
    background: #a7a29c;
    border-radius: 50%;
    margin-top: 1rem;
}

@media (min-width: 800px) {
    .home {
        flex-direction: row;
        justify-content: space-between;
        gap: 3rem;
    }
    .home .title,
    .home .person {
        width: 50%;
        margin-top: 0;
        text-align: left;
        font-size: 25px;
    }
    .home h1 {
        font-size: 6rem;
    }
    #name {
        color: #a7a29c;
    }
    .title {
        border-radius: 50%;
    }
    .imgDev {
        width: 80%;
    }
}